import Api from '../../apis'

export default {
  login: (data) => {
    return Api.post('/login', {username: data.username, password: data.password})
  },

  getUser: () => {
    const accessToken = localStorage.getItem('accessToken')
    return Api.get('/user/all', { headers: { Authorization: `Bearer ${accessToken}` } })
  },

  register: () => {
    return Api.post('register')
  }
}
