<template>
  <div class="wrapper" v-if="!isLoggedIn">
    <div class="form-login">
      <div class="form-login__left">
        <img src="../assets/login_fg.png" alt=""/>
      </div>
      <div class="form-login__right">
        <form v-on:submit.prevent="submit" v-if="!isLoggedIn">
         <h1 style="color:#25aa97">User Login</h1>
         <p>{{ message }}</p>
         <input class="input-text" type="text" name="username" v-model="username" />
         <input class="input-text" type="password" name="password" v-model="password" />
         <input class="btn btn-submit" type="submit" value="Login" @click="login" />
        </form>
      </div>
    </div>
  </div>
  <div v-if="isLoggedIn">
    <h1>Home page</h1>
    <h1>{{ users ? users[0].username : null }}</h1>
  </div>
</template>
<script>
/* import { mapGetters, mapMutations, mapState } from 'vuex' */
/* import { onMounted, ref } from 'vue' */
/* import { reactive } from 'vue' */
import { mapGetters, mapMutations } from 'vuex'
import Api from '../apis/modules/auth'

export default {
  name: 'Home',
  computed: {
    ...mapGetters(['isLoggedIn', 'getAccessToken']),
  },
  created() {
    this.fetchData()
  },
  data() {
    return {
      username: '',
      password: '',
      message: '',
      users: ''
    }
  },
  methods: {
    ...mapMutations(['setAuth', 'setAccessToken', 'setRefreshToken']),
    async login() {
      const res = await Api.login({username: this.username, password: this.password})
      const {accessToken, refreshToken} = await res.data

      if (accessToken && refreshToken) {
        localStorage.setItem('auth', true)
        localStorage.setItem('accessToken', accessToken)
        localStorage.setItem('refreshToken', refreshToken)

        this.setAuth(true)
        this.setAccessToken(accessToken)
        this.setRefreshToken(refreshToken)
        this.fetchData()
      }

      this.message = res.data.err
    },
    fetchData() {
      if (this.isLoggedIn) {
        Api.getUser().then(users => { this.users = users.data })
      }
    }
  }
}
</script>

